.page-container {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.page-container .table-container {
  width: 85%;
  margin: 5%;
  height: 70%;
}
.page-container .table-container .header-row {
  display: flex;
  align-items: center;
  width: 100%;
  justify-content: space-between;
}
.page-container .table-container .header-row input {
  padding: 1%;
  border: none;
  border-radius: 2%;
  width: 20%;
  font-family: inherit;
}
.page-container .table-container .header-row input:focus {
  outline: none;
}
.page-container .table-container .table {
  max-height: 60vh;
  overflow-y: scroll;
}
.page-container .table-container .PuywH:hover {
  background-color: rgba(0, 0, 0, 0.4) !important;
  color: white !important;
  cursor: pointer;
}
.page-container .table-container .team-logo {
  width: 70px;
  height: 70px;
}
.page-container .no-results {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 20%;
}
.page-container .no-results h1 {
  font-size: 45px;
}
.page-container .no-results p {
  font-size: 30px;
}
.page-container .no-results button {
  font-family: inherit;
  padding: 1%;
  border-radius: 20px;
  border: none;
  font-weight: bold;
  color: #435159;
  background-color: white;
}
.page-container .no-results button:hover {
  background-color: #435159;
  color: white;
  cursor: pointer;
}
.page-container .no-results button:focus {
  outline: none;
}
@media (max-width: 764px) {
  .header-row {
    flex-direction: column;
    margin-bottom: 2%;
  }
  .header-row input {
    width: 70% !important;
  }
}
